import products from '@/components/Products'
export default {
    name: 'theme',
    data() {
        return {
            banner: '',
            themeList: []
        }
    },
    activated() {
        this.getAllTheme()
    },
    methods: {
        getAllTheme() {
            this.$http
                .get(
                    '/theme/all'
                )
                .then(res => {
                    let themeData = res.data.data.filter(item => {
                        return item.id === +this.$route.query.id
                    })
                    this.banner = themeData[0].head_img.url
                    this.themeList = themeData[0].products
                })
        }
    },
    components: {
        products
    }
}



